import React from "react";
import "./AddItemInput.css";

const AddItemInput = ({ label, placeholder, big, handleChange, name }) => {
  return (
    <div className="additeminput-container">
      <p>{label}</p>
      <input
        name={name}
        onChange={handleChange}
        className={big ? "big" : null}
        type="text"
        placeholder={placeholder}
      ></input>
    </div>
  );
};
export default AddItemInput;
